import React, { useState } from "react";
import { TextField, Button } from "@material-ui/core/";
function WpRestApi() {
  const [formVal, setFormVal] = useState({
    username: "",
    email: "",
  });

  // Input 処理。
  const handleInputChange = (e) => {
    setFormVal({ ...formVal, [e.target.name]: e.target.value });
  };
  const handelPost = (e) => {
    e.preventDefault();
    fetch(
      "https://favotubers.com/wp-json/contact-form-7/v1/contact-forms/31/feedback",
      {
        method: "POST",
        headers: new Headers({
          "Content-type": "application/x-www-form-urlencoded",
        }),
        body: new URLSearchParams(formVal).toString(),
      }
    )
      .then((d) => console.log(d.json()))
      .catch((e) => alert(e));
  };
  return (
    <div>
      <form
        action="https://favotubers.com/wp-json/contact-form-7/v1/contact-forms/31/feedback"
        method="POST"
      >
        <div className="form-group">
          <label htmlFor="username">userneme</label>
          <input type="text" id="username" name="username" placeholder="" />
        </div>
        <div className="form-group">
          <label htmlFor="email">email</label>
          <input type="text" id="email" name="email" placeholder="" />
        </div>
        <button type="submit" class="btn btn-primary">
          Submit
        </button>
      </form>
      <hr />
      <h2>fetch api</h2>
      <form validate>
        <TextField
          id="standard-error-helper-text"
          required
          label="username"
          name="username"
          value={formVal.username}
          onChange={handleInputChange}
          error={!formVal.username}
          helperText={!formVal.username && "Required."}
        />
        <TextField
          id="filled-basic"
          required
          label="email"
          name="email"
          value={formVal.email}
          variant="filled"
          onChange={handleInputChange}
          error={!formVal.email}
          helperText={!formVal.email && "Required."}
        />

        <Button variant="contained" color="primary" onClick={handelPost}>
          Primary
        </Button>
      </form>
    </div>
  );
}

export default WpRestApi;
